import React from 'react';

function User({ user }) {
    return (
        <div>
            <b>{user.username}</b> <span>({user.email})</span>
        </div>
    );
}

function UserList() {
    const users = [
        {
            id: 1,
            username: 'subin',
            email: 'subin@example.com'
        },
        {
            id: 2,
            username: 'user1',
            email: 'user1@example.com'
        },
        {
            id: 3,
            username: 'user2',
            email: 'user2@example.com'
        }
    ];

    return (
        <div>
            {users.map(user => (
                <User user={user} key={user.id} />
            ))}
        </div>
    );
}

export default UserList;