import React, { useState } from 'react';
import './hdro.css';
import User from './UserList';
// Bootstrap
import Image from 'react-bootstrap/Image'
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Carousel from 'react-bootstrap/Carousel';
import ListGroup from 'react-bootstrap/ListGroup';
import Table from 'react-bootstrap/Table';
import Accordion from 'react-bootstrap/Accordion';
import { useSelector } from 'react-redux';

function hdro() {

    return (
        <Container className='main' fluid="lg">
            <Row>
            </Row>
            <Row>
                <Col>{Discription()}</Col>
                {/* {Discription()} */}
            </Row>
        </Container>
    )
}
function Discription() {
    return (
        <div>
            <h3 className='title'>농업용수 처리기 (역삼투압)</h3>
            <Image className='img-product' src="./img/Product/hdro803.png" rounded />
            <h3 className='title'>주요 특징 및 특장점</h3>
            <ul className='list-spec'>
                <li>사용 조건에 따라 사용량, 수질 상황에 맞춤 설계</li>
                <li>시스템 프레임 적용으로 향후 정수 용량 추가 가능</li>
                <li>컴팩트한 디자인으로 유지보수의 편의성</li>
                <li>고압 접액부 스테인레스 또는 고압 배관 사용으로 내구성 증대</li>
                <li>터치 스크린 컨트롤러로 작동 상황 실시간 모니터링 및 제어 가능</li>
                <li>전자동 수위 제어 시스템 및 경보 기능</li>
                <li>정수 + 원수 희석 제어 가능</li>
                <li>품질 보증 및 신속한 A/S 대응</li>
                <li></li>
                <li></li>
                <li></li>
            </ul>
            <h3 className='title'>모델별 사양</h3>
            <Model />
            <h3 className='title'>모델별 사진</h3>
            <ModelAccordion />
            <h3 className='title'>제품 외형도</h3>
            <Image src="./img/Product/hdro803.png" rounded />
            <h3 className='title'>공정흐름도(Process Flow Diagram)</h3>
            <Image src="./img/Product/hdro803.png" rounded />
            <h3 className='title'>주요 부품 구성</h3>

        </div>
    )
}

function Model() {

    let x = useSelector((state) => state.hdroModel)

    return (
        <Table striped bordered hover size="sm">
            <thead>
                <tr>
                    <th>모델명<br />(Model)</th>
                    <th>생산량<br />(톤 / 시간)</th>
                    <th>전기사양<br />(V / Hz)</th>
                    <th>펌프용량<br />(마력 / kW / Hz)</th>
                    <th>멤브레인/수량<br />(Module / EA)</th>
                    <th>제품크기/중량<br />(mm / Kg)</th>
                </tr>
            </thead>
            <tbody>
                {
                    x.map((a, i) =>
                        <tr key={i}>
                            <td>{x[i].name}</td>
                            <td>{x[i].capacity}</td>
                            <td>{x[i].electric}</td>
                            <td>{x[i].pump}</td>
                            <td>{x[i].filter}</td>
                            <td>{x[i].size}</td>
                        </tr>
                    )
                }
            </tbody>
        </Table>
    )
}

function ModelAccordion() {
    let x = useSelector((state) => state.hdroModel)

    return (

        <> {x.map((a, i) =>
            <Accordion defaultActiveKey="1">
                <Accordion.Item eventKey={x[i].key}>
                    <Accordion.Header>{x[i].name}</Accordion.Header>
                    <Accordion.Body>
                        Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do
                        <img
                            className="d-block w-100"
                            src="./img/Product/hdro803.png"
                            alt="Second slide"
                        />
                    </Accordion.Body>
                </Accordion.Item>
            </Accordion>
        )}</>


    )
}

function productCarousel() {
    return (
        <Carousel>
            <Carousel.Item>
                <img
                    className="d-block w-100"
                    src="./img/Product/hdro803.png"
                    alt="First slide"
                />
                <Carousel.Caption>
                    <h3>First slide label</h3>
                    <p>Nulla vitae elit libero, a pharetra augue mollis interdum.</p>
                </Carousel.Caption>
            </Carousel.Item>
            <Carousel.Item>
                <img
                    className="d-block w-100"
                    src="./img/Product/hdro803.png"
                    alt="Second slide"
                />

                <Carousel.Caption>
                    <h3>Second slide label</h3>
                    <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit.</p>
                </Carousel.Caption>
            </Carousel.Item>
            <Carousel.Item>
                <img
                    className="d-block w-100"
                    src="./img/Product/hdro803.png"
                    alt="Third slide"
                />

                <Carousel.Caption>
                    <h3>Third slide label</h3>
                    <p>
                        Praesent commodo cursus magna, vel scelerisque nisl consectetur.
                    </p>
                </Carousel.Caption>
            </Carousel.Item>
        </Carousel>
    )
}
function productList() {
    return (
        <div>
            <h2>농업용수 처리기</h2>
            <ListGroup>
                <ListGroup.Item>HDRO-401 (5톤/일)</ListGroup.Item>
                <ListGroup.Item>HDRO-402 (10톤/일)</ListGroup.Item>
                <ListGroup.Item>HDRO-801 (20톤/일)</ListGroup.Item>
                <ListGroup.Item>HDRO-802 (40톤/일)</ListGroup.Item>
                <ListGroup.Item>HDRO-803 (60톤/일)</ListGroup.Item>
            </ListGroup>
        </div>
    )
}

export default hdro;