import React from 'react';
import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import NavDropdown from 'react-bootstrap/NavDropdown';
import './Main.css';

function Navi() {
    return (
        <Navbar bg="light" variant="light">
            <Container>
                <Navbar.Brand href="/" className='brand'>
                </Navbar.Brand>
                <Nav className="me-auto">
                    <NavDropdown title="회사소개" id="basic-nav-dropdown">
                        <NavDropdown.Item href="company">회사소개/연혁</NavDropdown.Item>
                        <NavDropdown.Item href="location">오시는길</NavDropdown.Item>
                    </NavDropdown>
                    <NavDropdown title="제품정보" id="basic-nav-dropdown">
                        <NavDropdown.Item href="product">전체 제품 보기</NavDropdown.Item>
                        <NavDropdown.Divider />
                        <NavDropdown.Item href="hdro">농업용 정수기</NavDropdown.Item>
                        <NavDropdown.Item href="jet">양액공급기</NavDropdown.Item>
                        <NavDropdown.Item href="fog">무인방제기</NavDropdown.Item>
                        <NavDropdown.Divider />
                        <NavDropdown.Item href="cases">설치사례</NavDropdown.Item>
                    </NavDropdown>
                    <NavDropdown title="기술자료" id="basic-nav-dropdown">
                        <NavDropdown.Item href="manual">사용설명서</NavDropdown.Item>
                        <NavDropdown.Item href="specification">시방서</NavDropdown.Item>
                        <NavDropdown.Item href="certification">특허 및 인증서</NavDropdown.Item>
                        <NavDropdown.Item href="catalogue">카탈로그</NavDropdown.Item>
                    </NavDropdown>
                    <NavDropdown title="고객지원" id="basic-nav-dropdown">
                        <NavDropdown.Item href="as">A/S 신청</NavDropdown.Item>
                        <NavDropdown.Item href="inquiry">견적문의</NavDropdown.Item>
                        <NavDropdown.Item href="news">공지사항</NavDropdown.Item>
                    </NavDropdown>
                </Nav>
            </Container>
        </Navbar>
    );
}

export default Navi;

