import React from 'react';
import './Main.css';
import Carousel from 'react-bootstrap/Carousel';

let copy = ['한들이엔지(주)에서는 최고의 솔루션을 제공하기 위해 노력하겠습니다.'];

function Main() {
    return (
        <Carousel>
            <Carousel.Item interval={1000}>
                <img
                    className="d-block w-100"
                    src="/img/mainbg.jpg"
                    //url(/img/mainbg.jpg);
                    alt="First slide"
                />
                <Carousel.Caption>
                    <h3>First slide label</h3>
                    <p>{copy[0]}</p>
                </Carousel.Caption>
            </Carousel.Item>
            <Carousel.Item interval={500}>
                <img
                    className="d-block w-100"
                    src="/img/mainbg.jpg"
                    alt="Second slide"
                />
                <Carousel.Caption>
                    <h3>Second slide label</h3>
                    <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit.</p>
                </Carousel.Caption>
            </Carousel.Item>
            <Carousel.Item>
                <img
                    className="d-block w-100"
                    src="/img/mainbg.jpg"
                    alt="Third slide"
                />
                <Carousel.Caption>
                    <h3>Third slide label</h3>
                    <p>
                        Praesent commodo cursus magna, vel scelerisque nisl consectetur.
                    </p>
                </Carousel.Caption>
            </Carousel.Item>
        </Carousel>
    );
}

export default Main;

//copy1 한들이엔지(주)에서는<br />최고의 솔루션을 제공하기 위해 노력하겠습니다.</p>